"use client";
import { useScroll, useTransform, motion } from "motion/react";
import React, { useEffect, useRef, useState } from "react";

// Remove TypeScript interface (not valid in .jsx)
export const Timeline = ({ data }) => {
  const ref = useRef(null);
  const containerRef = useRef(null);
  const [height, setHeight] = useState(0);

  useEffect(() => {
    if (ref.current) {
      const rect = ref.current.getBoundingClientRect();
      setHeight(rect.height);
    }
  }, [ref]);

  const { scrollYProgress } = useScroll({
    target: containerRef,
    offset: ["start 10%", "end 50%"],
  });

  const heightTransform = useTransform(scrollYProgress, [0, 1], [0, height]);
  const opacityTransform = useTransform(scrollYProgress, [0, 0.1], [0, 1]);

  return (
    <div
      className="w-full font-sans bg-white dark:bg-neutral-950 md:px-10"
      ref={containerRef}
    >
      <div className="mx-auto py-20 px-4 max-w-7xl md:px-8 lg:px-10">
        <h2 className="mb-4 max-w-4xl text-lg text-black dark:text-white md:text-4xl">
          Changelog from my journey
        </h2>
        <p className="max-w-sm text-neutral-700 text-sm dark:text-neutral-300 md:text-base">
          I&apos;ve been working on Aceternity for the past 2 years. Here&apos;s
          a timeline of my journey.
        </p>
      </div>
      <div ref={ref} className="relative mx-auto pb-20 max-w-7xl">
        {data.map((item, index) => (
          <div key={index} className="flex justify-start pt-10 pt-40 md:gap-10">
            <div className="z-40 sticky top-40 flex flex-col items-center self-start max-w-xs w-full md:flex-row lg:max-w-sm">
              <div className="absolute left-3 flex items-center justify-center h-10 w-10 bg-white rounded-full dark:bg-black md:left-3">
                <div className="p-2 h-4 w-4 bg-neutral-200 rounded-full border-neutral-300 dark:bg-neutral-800 border dark:border-neutral-700" />
              </div>
              <h3 className="hidden pl-20 text-xl font-bold text-neutral-500 text-5xl dark:text-neutral-500 md:block">
                {item.title}
              </h3>
            </div>

            <div className="relative pl-20 pr-4 w-full md:pl-4">
              <h3 className="block mb-4 text-2xl text-left font-bold text-neutral-500 dark:text-neutral-500 md:hidden">
                {item.title}
              </h3>
              {item.content}{" "}
            </div>
          </div>
        ))}
        <div
          style={{
            height: height + "px",
          }}
          className="overflow-hidden absolute left-8 top-0 w-[2px] bg-[linear-gradient(to_bottom,var(--tw-gradient-stops))] from-transparent from-[0%] via-neutral-200 to-transparent to-[99%] dark:via-neutral-700 [mask-image:linear-gradient(to_bottom,transparent_0%,black_10%,black_90%,transparent_100%)] md:left-8"
        >
          <motion.div
            style={{
              height: heightTransform,
              opacity: opacityTransform,
            }}
            className="absolute inset-x-0 top-0 w-[2px] bg-gradient-to-t from-purple-500 via-blue-500 to-transparent from-[0%] via-[10%] rounded-full"
          />
        </div>
      </div>
    </div>
  );
};
